.headerImages{
 width: 100%;
 height:300px;
}

.headerLine{
  margin:auto;
}
.horizontal-slider{
  color:blue;
  background-color:red;

}
.sendButt{
  margin-top:10px;
  display:flex;
  width:15%;
  height: 10vh;
  margin:0 auto;
}
.sendButt button{
  border-radius: 10px;
  border: 2px solid blue;
  align-self: center;
  width:100%;
  height:100%;
  font-size: 3vh;
}
.allLabels{
  display:flex;
  width:100%;
  height:400px;
  align-items: center;
  justify-content: space-between;
}
@media(max-width:480px){
  .allLabels{
    display:flex;
    flex-flow: column;
  }
  .sendButt button{
    font-size: 1.8vh;
  }
}
.winepic{
  max-width: 400px;

  object-fit: contain;
}
.Loader{
  max-width: 400px;
}
.firstcol{
  width:100%;
}

.thirdcol{
  width:100%;
}
.item{
  margin-top: 20px;
}